import React, {FC, useEffect, useRef, useState} from 'react'
import {SORT_VALUES} from 'constants/app'
import {setSortParams} from 'store/filter/filter.slice'
import {SortType} from 'store/filter/filter.types'
import {useActions} from 'hooks/useActions'
import {useWhyDidYouUpdate} from 'ahooks'
import {URLSearchParamsInit} from 'react-router-dom'

interface SortProps {
    setSearchParams: (
        nextInit: URLSearchParamsInit | ((prev: URLSearchParams) => URLSearchParamsInit),
    ) => void
    sortParams: SortType
}

export const Sort: FC<SortProps> = ({sortParams, setSearchParams}) => {
    const actions = useActions({setSortParams})
    const sortRef = useRef<HTMLDivElement>(null)
    const [isVisible, setIsVisible] = useState<boolean>(false)

    useWhyDidYouUpdate('sort', {sortParams})

    const clickHandler = (sortParam: SortType) => {
        actions.setSortParams(sortParam)

        setSearchParams(prevParams => {
            prevParams.set('sortBy', sortParam.queryParamName)
            return prevParams
        })

        setIsVisible(!isVisible)
    }

    const outSideClickHandler = (e: MouseEvent) => {
        if (sortRef.current && !e.composedPath().includes(sortRef.current)) {
            setIsVisible(false)
        }
    }

    useEffect(() => {
        document.body.addEventListener('click', outSideClickHandler)

        return () => {
            document.body.removeEventListener('click', outSideClickHandler)
        }
    }, [])

    return (
        <div ref={sortRef} className="sort">
            <div className="sort__label">
                <svg
                    width="10"
                    height="6"
                    viewBox="0 0 10 6"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                >
                    <path
                        d="M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z"
                        fill="#2C2C2C"
                    />
                </svg>
                <b>Сортировка по:</b>
                <span onClick={() => setIsVisible(!isVisible)}>{sortParams.queryParamName}</span>
            </div>
            {isVisible && (
                <div className="sort__popup">
                    <ul>
                        {SORT_VALUES.map(param => (
                            <li
                                key={param.queryParamName}
                                className={
                                    param.queryParamName === sortParams.queryParamName
                                        ? 'active'
                                        : ''
                                }
                                onClick={() => clickHandler(param)}
                            >
                                {param.queryParamName}
                            </li>
                        ))}
                    </ul>
                </div>
            )}
        </div>
    )
}
